<template>
	<view class="">
		<Head title='动漫IP NFT' />
		<view class="content_box">
			<view class="classification fontS32 flexJBC fw6">
				 <view class="l " :class="!willLine?'active':''" @click="willLine=false">售卖中</view>
				 <view class="r" :class="willLine?'active':''" @click="willLine=true"> 即将上线</view>
			</view>
		</view>
		<water-flow-box ref="wfp" @click="choose" :colunmNumber="colunmNumber" :flowList="list" :columnGap="16" style='padding: 0 20rpx;'></water-flow-box>
	</view>
</template>

<script>
	import waterFlowBox from '@/componentes/hd_list/hd_list.vue'
	const data = require('@/componentes/hd_list//data.json');  
	export default{
		data(){
			return{
				willLine:false, //是否是售卖中
				
				colunmNumber: 2, //列
								page: 1,
								start: 0,
								end: 0,
								list: [], // 列表
			}  
		},
		components:{waterFlowBox},
		onLoad() {
					this.getList();
				},
				onReachBottom() {
					this.page++;
					this.getList();
				},
				methods: {
					// 选中
					choose(item) {
						// item 返回选中 JSON 对象
						console.log(item)
					},
					// 模拟加载数据
					getList() {
						if (this.list.length < data.list.length) {
							uni.showLoading({
								title: '加载中...'
							})
							setTimeout(() => {
								//this.$refs.wfp.externalRefrensh(); //清理掉原有加载数据 避免追加
								this.end = this.page * 10; //假数据比较少 , 可以调小一点
								this.list = this.list.concat(data.list.slice(this.start, this.end));
								console.log(this.list,'--------------------')
								this.start = this.end;
								uni.hideLoading();
							}, 1000)
						}
					}
				}
	
	}
</script>

<style lang="scss" scoped>
	.content_box{
		padding: 30rpx 40rpx 0rpx;
		box-sizing: border-box;
	}
	.classification{
		margin-bottom: 42rpx;
		border-bottom: 1px solid #D4D4D4;
		view{
			width: 50%;
			text-align: center;
			padding-bottom: 32rpx;
			color: #999999;
			position: relative;
		}
		.active{
			color: #0F172C;
			&:before{
				content: '';
				position: absolute;
				bottom: -4rpx;
				width: 100%;
				height: 6rpx;
				background: #0F172C;
				left: 0%;
			}
				
		}
	}
	

</style>